.wp-block-wpdev-product-parade-block {
    .add_to_cart_button{
        margin-top: 16px;
        outline: none;
        width: 132px;
    }
}

.product-parade-block-tab-panel{

    .product-parade-block-layouts{
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        justify-content: center;
        align-items: center;
        >.product-parade-block-layout{
            >.product-parade-block-layout-icon-area{
                border: 2px solid rgb(139, 143, 143);
                width: fit-content;
                padding: 0px 8px;
                border-radius: 5px;
                cursor: pointer;
                position: relative;

                &.active{
                    border: 2px solid rgb(1, 83, 83);
                }

                img{
                    width: 80px;
                }
                .layout-yes-icon{
                    color: rgb(1, 83, 83);
                    position: absolute;
                    right: 5px;
                    top: 5px;
                }
            }
            .layout-label{
                text-align: center;
                color: rgb(1, 83, 83);
                font-weight: 500;
                margin: 0;
            }
        }
    }

	> .components-tab-panel__tabs{
		.components-button{
			background-color: #e8e8e8;
			color: #000000;
			border-bottom: 2px solid rgb(1, 83, 83);
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50%;
			.product-parade-block-tab-panel-title{
				display: flex;
				justify-content: center;
				align-items: center;
				gap: 5px;
			}
			&.product-parade-block-active-tab{
				background-color: rgb(1, 83, 83);
				color: white;
			}
		}
	}
    .product-parade-block-tabs-content{
        padding: 20px;        
    }
    .components-panel__body .components-button:focus {
    outline: none;
    box-shadow: none;
    }
    .components-form-toggle:focus{
            outline: none;
    }
    .product-parade-block-button-group{
        background-color: #f3f3f3;
        padding: 0;
        display: flex;
        border: 1px solid grey;
        justify-content: space-between;
        margin-bottom: 20px;
        >.product-parade-block-sidebar-button{
            &.active-button{
                background-color: rgb(1, 83, 83);
                color: #fff;
                outline: none;
            }
            color: rgb(1, 83, 83);
            font-weight: 500;
            text-align: center;
            width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 0px;
            outline: none;
        }
        .components-button:focus{
            outline: none;
            box-shadow: none;
        }
    }

    .product-parade-block-sidebar-label-text{
        font-size: 13px;
        color: #444444;
        font-weight: 500;
        margin: 5px auto;
    }
    .block-editor-panel-color-gradient-settings{
        padding: 0px;
        margin-bottom: 10px;
    }
    .components-range-control{
        margin-top: 20px;
    }

    .my-range-control-header-area{
        display: flex;
        justify-content: space-between;
        .my-range-control-label{
            color: #757575;
            display: inline-block;
            font-size: 11px;
            font-weight: 700;
            margin-bottom: 8px;
            text-transform: uppercase;
        }

    }

    .ppb-units{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
        
        span{
            border: 1px solid #8c8f94;
            color: #8c8f94;
            height: 20px;
            width: 23px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 2px;
            cursor: pointer;
            text-transform: capitalize;
            font-size: 11px;
        }

        span:hover{
            color: rgb(1, 83, 83);
            border-color: rgb(1, 83, 83);
            .ppb-units-btn{
                visibility: visible;
            }
        }

        .ppb-units-btn{
            opacity: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            box-shadow: 2px 2px 8px 0 rgba(0, 0, 0, .15);
            width: 23px;
            position: absolute;
            top: 23px;
            z-index: 999;
            background-color: #f3f3f3;
            padding: 0;
            gap: 2px;
            button{
                height: fit-content;
                font-size: 11px;
                padding: 0px 3px;
                margin: 0;
            }
            button:hover{
                background-color: rgb(1, 83, 83);
                color: white;
            }
        }
    }

    .components-range-control__wrapper{
        .components-range-control__track{
            background-color: rgb(1, 83, 83);
        }
        .components-range-control__thumb-wrapper{
            span{
                background-color: rgb(1, 83, 83);
            }
        }
    }

    // .block-editor-panel-color-gradient-settings{
    //     padding: 0px;
    //     margin-bottom: 40px;
    // }

}